}
.form-login .con-h5 header {
  height: 38px;
  line-height: 48px;
  border: 1px solid #ddd;
  background: #fff;
  padding: 14px 15px 12px
}
.form-login .con-h5 header .logo {
  display: block;
  width: 63px;
  height: 28.8px;
  float: left;
  margin-top: 3px;
  margin-right: 12px;
  background: url(https://static-s.bilibili.com/account/img/logo-mobile.png);
  background-size: 63px 28.8px
}
.form-login .con-h5 header .logoText {
  height: 42px;
  border-left: 2px solid #de698c;
  color: #de698c;
