}
.form-login .con-h5 .form {
  padding: 15px
}
.form-login .con-h5 .form .item .input-wrp {
  border-radius: 6px;
  margin-bottom: 10px;
  background: #fff;
  width: 100%;
  border: 1px solid #ddd;
  height: 45px;
  line-height: 45px;
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: relative
}
.form-login .con-h5 .form .item input {
  border-radius: 6px;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 1em;
  left: 0;
  top: 0;
  bottom: 0;
  padding: 10px;
  border: 0
}
.form-login .con-h5 .form .item .text .tips {
  padding: 0 0 8px 2px;
  color: #de698c
}
.form-login .con-h5 .form .item.password .text .tips {
  float: left
}
.form-login .con-h5 .form .item.password .text .forget-password {
  float: right;
  padding: 0 0 8px 2px
}
.form-login .con-h5 .form .item .captcha {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  max-width: 35%
}
.form-login .con-h5 .form .sns-tips {
  height: 20px;
  padding: 0 0 20px 2px;
  color: #de698c
}
.form-login .con-h5 .form .btn-box .btn {
  display: block;
  border: 1px solid #00a1d6;
  text-align: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  border-radius: 32px;
  padding: 12px;
  margin-bottom: 10px;
  color: #00a1d6;
  background: #fff
}
.form-login .con-h5 .form .btn-box .btn.btn-login {
  background-color: #00a1d6;
  color: #fff
}
.form-login .con-h5 .form .sns {
  position: relative;
  text-align: center;
  margin-top: 20px;
  padding-top: 50px
}
.form-login .con-h5 .form .sns .btn {
  display: inline-block;
  width: 50px;
  height: 50px;
  padding: 0 5px;
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: 50px 50px
}
.form-login .con-h5 .form .sns .btn.weibo {
  background-image: url(data:image/png;base64,)
}
.form-login .con-h5 .form .sns .btn.qq {
  background-image: url(data:image/png;base64,)
}
.form-login .con-h5 .form .sns .splitter {
  border-top: 1px solid #999;
  top: 15px;
  left: 0;
  right: 0;
  position: absolute
}
.form-login .con-h5 .form .sns .splitter .title {
  color: #999;
  position: absolute;
  left: 50%;
  margin-left: -25%;
  top: -15px;
  height: 30px;
  width: 50%;
  text-align: center;
  line-height: 30px;
  background: #f5f5f5
}
.form-login .geetest {
  position: absolute;
  background-color: rgba(0, 0, 0, .5);
  width: 100%;
  left: 0;
  top: 0
}
.form-login .geetest .pop {
  position: relative;
  background-color: #fff;
  padding: 40px 20px 20px;
  margin: 100px auto;
  border-radius: 10px;
  max-width: 300px
}
.form-login .geetest .pop .tips {
  color: #757575;
  text-align: center;
  margin-top: 20px;
  font-size: 1rem
}
.form-login .geetest .pop .btn-close {
  position: absolute;
  width: 16px;
  height: 16px;
  right: 15px;
  top: 15px;
  overflow: hidden;
  cursor: pointer;
  -webkit-transition: all .3s;
  transition: all .3s
}
.form-login .geetest .pop .btn-close:before {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  -ms-transform: rotate(45deg)
}
.form-login .geetest .pop .btn-close:after,
.form-login .geetest .pop .btn-close:before {
  content: "";
  position: absolute;
  height: 1px;
  width: 100%;
  top: 50%;
  left: 0;
  margin-top: -1px;
  background: #aaa
}
.form-login .geetest .pop .btn-close:after {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  -ms-transform: rotate(-45deg)
}
.form-login .geetest .pop .btn-close:hover:after,
.form-login .geetest .pop .btn-close:hover:before {
  background: #666
}
.form-login .spinner {
  width: 50px;
  text-align: center
}
.form-login .spinner>div {
  width: 6px;
  height: 6px;
  background-color: #88c86b;
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: bouncedelay 1.4s infinite ease-in-out;
  animation: bouncedelay 1.4s infinite ease-in-out;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both
}
.form-login .spinner .bounce1 {
  -webkit-animation-delay: -.32s;
  animation-delay: -.32s
}
.form-login .spinner .bounce2 {
  -webkit-animation-delay: -.16s;
  animation-delay: -.16s
}
@-webkit-keyframes bouncedelay {
0%,
  80%,
  to {
    -webkit-transform: scale(0)
}
40% {
    -webkit-transform: scale(1)
}
}
@keyframes bouncedelay {
0%,
  80%,
  to {
    transform: scale(0);
    -webkit-transform: scale(0)
}
40% {
    transform: scale(1);
    -webkit-transform: scale(1)
}
}
.status-box {
  position: relative
}
.status-box .status {
  display: none;
  background-image: url(data:image/png;base64,);
  position: absolute;
  top: 8px;
  right: -25px;
  width: 20px;
  height: 20px
}
.status-box .status.error {
  display: block;
  background-position: -37px -38px
}
.qrcode-login {
  position: relative;
  width: 480px
}
.qrcode-login .qrcode-con {
  padding-top: 72px
}
.qrcode-login .qrcode-con .tv-icon {
