  height: 140px;
  background: url(//s1.hdslb.com/bfs/static/passport/static/img/loadTV.99606e2.gif) 50% no-repeat
}
.qrcode-login .qrcode-con .qrcode-box .qrcode-tips {
  z-index: 10;
  position: absolute;
  width: 175px;
  height: 190px;
  top: -50px;
  left: 50%;
  margin-left: -88px;
  background: url(//s1.hdslb.com/bfs/static/passport/static/img/qr-tips.2fcba15.png) no-repeat;
  background-color: #fff;
  opacity: 0;
  -webkit-transition: opacity .5s;
