}
.qrcode-login .qrcode-con .qrcode-box .status .overdue {
  background: url(data:image/png;base64,) 8px 3px no-repeat #fff;
  border-radius: 3px;
  -webkit-box-shadow: 0 3px 10px rgba(0, 0, 0, .5);
          box-shadow: 0 3px 10px rgba(0, 0, 0, .5);
  cursor: pointer;
  margin: 40px auto;
  width: 60px;
  padding: 40px 0 5px;
  color: #0594c7;
  text-align: center
}
.qrcode-login .qrcode-con .qrcode-box .status .success {
  background: url(data:image/png;base64,) no-repeat;
  border-radius: 3px;
  cursor: pointer;
  margin: 40px auto;
  width: 60px;
  padding: 60px 5px 8px
}
.qrcode-login .qrcode-footer {
  width: 480px;
  height: 155px;
  background: url(//s1.hdslb.com/bfs/static/passport/static/img/2233login.af9c53d.png) 0 -7px no-repeat
}
.qrcode-login .qrcode-footer .status-txt {
  padding-top: 20px;
  text-align: center;
  color: #222;
  font-size: 18px;
  letter-spacing: 3px;
  margin-bottom: 5px
}
.qrcode-login .qrcode-footer .app-link,
.qrcode-login .qrcode-footer .suc-txt {
  text-align: center;
  color: #717171;
  font-size: 14px
}




